:root {

	/* Z-index levels */
}

.root {
	border: 1px solid #424242;
	background: #fff;
	max-height: 400px;
	overflow-y: auto;
	position: relative;
}

.focused {}

.option {
	cursor: pointer;
}

.focused .activeOption,
.root:focus .activeOption {
	background-color: #34495e;
	color: #fff;
}

.disabledOption {
	background-color: #bbb;
	cursor: default;
}

.selectedOption {
	background-color: #34495e;
	color: #fff;
}



